html{
  user-select: none;
  -webkit-user-drag: none;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 20px;
}

ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
  color: #333;
}

.App {
  width: 100%;
  margin: 0 auto;
}


.header {
  position: relative;
  width: 100%;
  border-bottom: #333 1px solid;
}

.header .menu {
  position: absolute;
  top: 10px;
  right: 0;
}

.header .link {
  border: 1px solid #333;
  padding: 10px;
  margin-right: 30px;
  background-color: #efefef;
  font-weight: bold;
  border-radius: 12px;
}
.openH{
  margin-left:80px;
}

.list{
 position: relative;
  width:20%;
  height: 850px;
border-right: 1px black solid;
display: flex;
text-align: center;
justify-content:flex-start;
overflow-y: scroll;
flex-direction:column;
align-items: center;
padding: 0;
line-height: 50px;
float:left;
}
::-webkit-scrollbar {
  width: 8px;  /* 스크롤바의 너비 */
  opacity: 0;
}

::-webkit-scrollbar-thumb {
  height: 30%; /* 스크롤바의 길이 */
  background-color: #acacac; /* 스크롤바의 색상 */
  opacity: 0.3;
  border-radius: 10px;
}

::-webkit-scrollbar-track {
  background: rgb(255, 255, 255,);  /*스크롤바 뒷 배경 색상*/
}


.bigMenu{
  background-color: rgb(195, 195, 195);
  width:90%;
  height: 50px;
  border-radius: 8px;
  margin-top: 20px;
  font-style: oblique;
  color: rgb(255, 255, 255);
  font-size: larger;
}
